
let utils = {};

utils.extend = function(dest) {
    var i, j, len, src;
    //TODO:arguments已过时，待用不定参数替代：http://www.infoq.com/cn/articles/es6-in-depth-rest-parameters-and-defaults/
    for (j = 1, len = arguments.length; j < len; j++) {
        src = arguments[j];
        for (i in src) {
            dest[i] = src[i];
        }
    }
    return dest;
};

export default {
    utils
};


